/**
 * Request 0.0.9
 * @Class Request
 * @description luch-request 0.0.9 http
 * @Author lu-ch
 * @Date 2019-08-20
 * @Email webwork.s@qq.com
 * http://ext.dcloud.net.cn/plugin?id=392
 */
export default class Request {
  config = {
    baseUrl: '',
    header: {
      'Content-Type': 'application/json;charset=UTF-8'
    },
    method: 'GET',
    dataType: 'json',
    responseType: 'text'
  }

  static posUrl (url) { /* url */
    return /(http|https):\/\/([\w.]+\/?)\S*/.test(url)
  }

  /**
   * @property {Function} request 
   * @property {Function} response 
   * @type {{request: Request.interceptor.request, response: Request.interceptor.response}}
   */
  interceptor = {
    /**
     * @param {Request~requestCallback} cb - ,config, cancel=> {return config}config,
     */
    request: (cb) => {
      if (cb) {
        this.requestBeforeFun = cb
      }
    },
    /**
     * @param {Request~responseCallback} cb 
     * @param {Request~responseErrCallback} ecb 
     */
    response: (cb, ecb) => {
      if (cb && ecb) {
        this.requestComFun = cb
        this.requestComFail = ecb
      }
    }
  }

  requestBeforeFun (config) {
    return config
  }

  requestComFun (response) {
    return response
  }

  requestComFail (response) {
    return response
  }

  /**
   * @Function
   * @param {Request~setConfigCallback} f - 
   */
  setConfig (f) {
    this.config = f(this.config)
  }

  /**
   * @Function
   * @param {Object} options - 
   * @prop {String} options.url - 
   * @prop {Object} options.data - 
   * @prop {Object} [options.responseType = config.responseType] [text|arraybuffer] - 
   * @prop {Object} [options.dataType = config.dataType] -  json JSON.parse
   * @prop {Object} [options.header = config.header] - header
   * @prop {Object} [options.method = config.method] - 
   * @returns {Promise<unknown>}
   */
  request (options = {}) {
    options.baseUrl = this.config.baseUrl
    options.dataType = options.dataType || this.config.dataType
    options.responseType = options.responseType || this.config.responseType
    options.url = Request.posUrl(options.url) ? options.url : (options.baseUrl + options.url)
    options.data = options.data || {}
    options.header = options.header || this.config.header
    options.method = options.method || this.config.method
    return new Promise((resolve, reject) => {
      let next = true
      let _config = null
      options.complete = (response) => {
        let statusCode = response.statusCode
        response.config = _config
        if (statusCode === 200) { // 
          response = this.requestComFun(response)
          resolve(response)
        } else {
          response = this.requestComFail(response)
          reject(response)
        }
      }
      let cancel = (t = 'handle cancel', config = options) => {
        let err = {
          errMsg: t,
          config: config
        }
        reject(err)
        next = false
      }

      _config = { ...this.requestBeforeFun(options, cancel) }
      if (!next) return
      uni.request(_config)
    })
  }

  get (url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'GET',
      ...options
    })
  }

  post (url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'POST',
      ...options
    })
  }


  put (url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'PUT',
      ...options
    })
  }



  delete (url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'DELETE',
      ...options
    })
  }



  connect (url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'CONNECT',
      ...options
    })
  }



  head (url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'HEAD',
      ...options
    })
  }



  options (url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'OPTIONS',
      ...options
    })
  }



  trace (url, data, options = {}) {
    return this.request({
      url,
      data,
      method: 'TRACE',
      ...options
    })
  }

}
/**
 * setConfig
 * @return {Object} - config
 * @callback Request~setConfigCallback
 * @param {Object} config - config
 */
/**
 * 
 * @return {Object} - config
 * @callback Request~requestCallback
 * @param {Object} config - config
 * @param {Function} [cancel] - 
 */
/**
 * 
 * @return {Object} - response
 * @callback Request~responseCallback
 * @param {Object} response -  response
 */
/**
 * 
 * @return {Object} - response
 * @callback Request~responseErrCallback
 * @param {Object} response -  response
 */
