/**
 * keykeyobjkey
 * keyobj
 */
export const hasKey = (obj, key) => {
  if (key) return key in obj
  else {
    let keysArr = Object.keys(obj)
    return keysArr.length
  }
}

/**
 * @param {*} obj1 
 * @param {*} obj2 
 * @description 
 */
export const objEqual = (obj1, obj2) => {
  const keysArr1 = Object.keys(obj1)
  const keysArr2 = Object.keys(obj2)
  if (keysArr1.length !== keysArr2.length) return false
  else if (keysArr1.length === 0 && keysArr2.length === 0) return true
  /* eslint-disable-next-line */
  else return !keysArr1.some(key => obj1[key] != obj2[key])
}

/**
 * @param {*} obj1 new Date()
 * @param {*} obj2 new Date()
 * @description  
 */
export const  getInervalHour = (startDate, endDate) =>{
  var ms = endDate.getTime() - startDate.getTime();
  if (ms < 0) return 0;
  return Math.floor(ms/1000/60/60);
}

/**
 * @param [] array 
 * @param string target 
 * @description target
 *  obj.sort(compareObj('a'));//b
 */
export const  compareObj = (propertyName) =>{
  return function (obj1,obj2) {
    if(obj1[propertyName] > obj2[propertyName]){
        return 1;
    }else if(obj1[propertyName] < obj2[propertyName]){
        return -1;
    }else{
        return 0;
    }
 }

}

//
export const formatSeconds =(value) =>{
  let secondTime = parseInt(value);// 
  let minuteTime = 0;// 
  let hourTime = 0;// 
  if(secondTime > 60) {//60
      //60
      minuteTime = parseInt(secondTime / 60);
      //
      secondTime = parseInt(secondTime % 60);
      //60
      if(minuteTime > 60) {
          //60
          hourTime = parseInt(minuteTime / 60);
          //60
          minuteTime = parseInt(minuteTime % 60);
      }
  }

  let result = "" + parseInt(secondTime) + "";
  if(minuteTime > 0) {
    result = "" + parseInt(minuteTime) + "" + result;
  }
  if(hourTime > 0) {
    result = "" + parseInt(hourTime) + "" + result;
  }
  
  return result;
}

 //
 export const stringToDate =(dateStr, separator) =>{
  if (!separator) {
    separator = "-";
  }
  let dateArr = dateStr.split(separator);
  let year = parseInt(dateArr[0]);
  let month;
  //04
  if (dateArr[1].indexOf("0") == 0) {
    month = parseInt(dateArr[1].substring(1));
  } else {
    month = parseInt(dateArr[1]);
  }
  let day = parseInt(dateArr[2]);
  let date = new Date(year, month - 1, day);
  return date;
}

export const dataAdd =(interval,number,date) =>{
  switch(interval.toLowerCase()){
    case "y": return new Date(date.setFullYear(date.getFullYear()+number));
    case "m": return new Date(date.setMonth(date.getMonth()+number));
    case "d": return new Date(date.setDate(date.getDate()+number));
    case "w": return new Date(date.setDate(date.getDate()+7*number));
    case "h": return new Date(date.setHours(date.getHours()+number));
    case "n": return new Date(date.setMinutes(date.getMinutes()+number));
    case "s": return new Date(date.setSeconds(date.getSeconds()+number));
    case "l": return new Date(date.setMilliseconds(date.getMilliseconds()+number));
    }
}

//
export const dataDiff =(interval,date1,date2) =>{
  var long = date2.getTime() - date1.getTime(); //
  switch(interval.toLowerCase()){
  case "y": return parseInt(date2.getFullYear() - date1.getFullYear());
  case "m": return parseInt((date2.getFullYear() - date1.getFullYear())*12 + (date2.getMonth()-date1.getMonth()));
  case "d": return parseInt(long/1000/60/60/24);
  case "w": return parseInt(long/1000/60/60/24/7);
  case "h": return parseInt(long/1000/60/60);
  case "n": return parseInt(long/1000/60);
  case "s": return parseInt(long/1000);
  case "l": return parseInt(long);
  }
}

/**
 * @desc tools  
 * 
 * @params  num 
 * 
 * @author Andy Huang
 * 
 * @created 2019/08/05 10:43:32
 */
export const toThousands =(num) =>{
    num = (num || 0).toString(), result = '';
  while (num.length > 3) {
      result = ',' + num.slice(-3) + result;
      num = num.slice(0, num.length - 3);
  }
  if (num) { result = num + result; }
  return result;
}