import axios from '@/libs/api.request'
import config from '@/config'
//import Qs from 'qs'
let apiPath = '/clerp-app-api' //




/**
 * @description  NEW FOR UNI-APP
 * @params {token}
 */
export const getReportList = ({
	token
}) => {
	//debugger
	//
	let data = {
		token
	}

	return axios.request({
		url: `${apiPath}/report`,
		data,
		method: 'POST',
	})
}

